.switch {
    width: 45px;
    height: 28px;
    position: relative;
    display: inline-block;
}

.switch>input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #dddd;
    border-radius: 50px;
}

.slider:before {
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: white;
    bottom: 4px;
    top: 4px;
    left: 4px;
    border-radius: 100%;
    transition: 0.5s;
}

input:checked+.slider:before {
    -webkit-transform: translateX(18px);
            transform: translateX(18px);
}

input:checked+.slider {
    background-color: #1FBBFF;
}

